@import '../../styles/mixin.scss';
.registrations {
    text-align: center;
    padding-top: 30px;
    @include phone() {
        padding: 10px;
    }
}
.photo_edit{
    width: 350px;
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;
}
.logo {
    font-size: 28px;
    padding-bottom: 15px;
    display: block;
}

.and::before{
 content: '';
 display: block;
 width: 43%;
 height: 1px;
 background-color: rgba(170, 170, 170, 0.5);
 position: absolute;
 left: 0px;
 top: 0px;
 bottom: 0px;
 margin: auto;
}
.and{
    font-size: 15px;
    color:#aaa;
    margin: 10px 0px;
    position: relative;
}
.and::after{
    content: '';
    display: block;
    width: 43%;
    height: 1px;
    background-color: rgba(170, 170, 170, 0.5);
    position: absolute;
    right: 0px;
    top: 0px;
    bottom: 0px;
    margin: auto;
   }