@import '../../styles/mixin.scss';
.navbar{
    height: 60px;
    width: 100%;
    border-bottom: 1px solid #dbdbdb;
    background-color: #fff;
    position: fixed;
    z-index: 100;
    top: 0px;
}

.container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1080px;
    height: 100%;
    margin: 0 auto;
    padding: 0px 20px;
}