@import '../../styles/mixin.scss';
.detailedCard{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
    border: 1px solid #dbdbdb;
    box-shadow: 0 2px 4px rgba(0, 0, 0,.2);
    margin: 15px auto;
    @include tablet(){
        margin: 8px auto;
    }
}
.detailedCard__header{
    display: flex;
    align-items: center;
    justify-content: start;
    height: 60px;
    padding: 20px;
    background: #fff;
}
.detailedCard__img{
    max-height: 100%;
    width: 100%;
}
.detailedCard__btn{
    display:flex;
    padding: 10px;
    gap: 10px;
}
.detailedCard__likes{
    font-size: 16px;
    font-weight: 400;
    padding: 10px;
}
.detailedCard__icons{
    cursor: pointer;
    font-size: 18px;
}
.detailedCard__comments_btn_all{
    font-weight: 600;
    cursor: pointer;
}
.detailedCard__comments{
    display: flex;
    flex-direction: column;
    padding: 0px 10px 10px 10px;
}
.detailedCard__textarea{
    border: none;
    background-color: #fff;
    padding: 10px;
    outline: none;
    resize: none;
}