.form{
    display: flex;
    flex-direction: column;
    gap: 13px;
    width: 100%;
    max-width: 400px;
    padding: 15px;
    margin: 0 auto;
    border: 1px solid rgba(170, 170, 170, 0.3);
    background-color: #fff;
    box-shadow: 0 2px 4px rgba(0 ,0 ,0, .2), 0 8px 16px rgba(0,0,0,.2);

    & > input{
        padding: 12px;
        border-radius: 8px;
        font-size: 18px;
        border: none;
        outline: 1px solid rgba(170, 170, 170, .5);
        &:focus{
            outline: 1px solid blue;
        }
    }
    & > button{
        color: #fff;
        background-color: #1877f2;
        border: none;
        border-radius: 8px;
        font-size: 20px;
        font-weight: 600;
        line-height: 48px;
        padding: 0 16px;
        transition: .2s;
        &:hover{
            background-color: #0a6dee;
        }
    }
}