.loader{
    text-align: center;
}
.lds__ring {
    display: inline-block;
    position: relative;
    width: 50px;
    padding-top: 10px;
    height: 50px;
  }
  .lds__ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    margin: 8px;
    border: 5px solid #fff;
    border-radius: 50%;
    animation: lds__ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #2c5c30 transparent transparent transparent;
  }
  .lds__ring div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .lds__ring div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .lds__ring div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes lds__ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }