.userBadge{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.userBadgeLink{
    text-decoration: none;
}
.userBadge__img{
    width: 25px;
    height: 25px;
    margin-right: 10px;
    border-radius: 50%;
}
.notImg{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: #efefef;
}
.userBadge__name{
    font-size: 16px;
    font-weight: 500;
    color: #000;
    text-decoration: none;
}